@import "~theme/main";





























#app {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

main {
  flex: 1;
  color: #fff;
}

footer {
  .dc-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;

    @media only screen and (min-width: $tablet) {
      flex-direction: row;
    }

    .dc-icon {
      margin: 1.5rem 0 2rem;
      font-size: 1.2rem;
      color: $primary-lightest;

      @media only screen and (min-width: $tablet) {
        margin: 0 2rem 0 1rem;
      }
    }
  }

  .dc-copyright {
   margin-top: 3.21rem;
  }

  a {
    color: $primary-lightest;

    &:hover {
      color: $primary-lightest;
      text-decoration: underline;
    }
  }
}
