@import "~theme/main";































.card {
  margin: 0 1rem;
  padding: 2rem 0 1rem;
  background-color: rgba(255, 255, 255, 0.69);
  height: 100%;

  @media only screen and (min-width: $tablet) {
    padding: 3rem 0 2rem;
  }

  .card-image {
    $dc-font-size: 4rem;

    .dc-icon {
      color: #7788af;
      font-size: $dc-font-size;
      font-weight: lighter;

      @media only screen and (min-width: $tablet) {
        font-size: $dc-font-size;
      }

      @media only screen and (min-width: $widescreen) {
        font-size: $dc-font-size * 1.5;
        margin-bottom: 1.64rem;
      }
    }
  }

  .dc-title {
    $dc-font-size: 1.4rem;
    font-size: $dc-font-size;

    @media only screen and (min-width: $widescreen) {
      font-size: $dc-font-size * 1.25;
    }
  }

  .dc-content {
    font-size: 1.1rem;
    text-align: center;
    color: $text-light;
  }
}
